import React from 'react'
import { Card, Button, CardHeader, CardBody } from 'reactstrap';
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";

import { validateEmail, clear, validateFields, customValidation } from '../../common.js'
import axios from '../../API.js'


class Work extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			id: '1', requesterid: '', startdate: new Date(), expiredate: new Date(), filename: '', filepath: '', loadRequester: [], isError: false, isUpdate: false,
			error: { requesterid: '', startdate: '', expiredate: '', filename: '' }
		}
	}
	onPreview = () => {
		this.setState({ filepath: "http://freelancetypers.com/images/works/" + this.state.filename })
	}
	onChange = (event) => {
		const data = this.state
		data[event.target.name] = event.target.value
		this.setState({ data })
		if (data.isError)
			this.onValidation(data)
	}
	onValidation = (ref = '') => {
		const emptyFields = [
			{ key: 'requesterid', errVal: 'Enter Requester' },
			{ key: 'filename', errVal: 'Enter Filename' },
			{ key: 'startdate', errVal: 'Enter Entry Date' },
			{ key: 'expiredate', errVal: 'Enter Expire Date' }
		]
		const data = validateFields(ref === '' ? this.state : ref, emptyFields)

		this.setState({ data })
		return data.isError
	}
	onSubmit = (event) => {
		const data = this.state
		if (!this.onValidation()) {
			if (this.state.isUpdate) {
				let data = this.state
				data.action = 'update'
				data.userid = '1'
				axios.post("/Works/Update", data).then(res => {
					if (res.data.status === 'success') {
						this.props.history.push('/Requesters')
						alert('Requester Details Updated Successfully..!!')
					}
				})
			}
			else {
				let data = this.state
				data.userid = '1'
				axios.post("/Works/Add", data).then(res => {
					console.log(res)
					if (res.data.status === 'success')
						this.props.history.push('/views/work')
					alert('Requester Details Added Successfully..!!')
				}).catch(err => {
					console.log(err)
				})
			}
		}
		// 	if(!res.isError) {
		// 		axios.post(this.props.common.serverURL+"/axios/Users/Add",data).then(res=>{
		// 			if(res.data.status==='success')
		// 				this.props.history.push({pathname:'/workerRegister',state:res.data.data.insertId})
		// 		}).catch(err=>{
		// 			console.log(err)
		// 		})
		// 	}
		// })
	}

	componentDidMount = () => {
		if (this.props.location.state !== undefined) {
			axios.get("/Requesters").then(res => {
				this.setState({ loadRequester: res.data.data })
			})

			axios.get("/Works", { params: { id: this.props.location.state.id } }).then(res => {
				let view = res.data.data[0];
				this.setState({ id: view.id, requesterid: view.requesterid, startdate: new Date(view.startDate), expiredate: new Date(view.expireDate), filename: view.fileName, isUpdate: true })
			})
		}
		else {
			axios.get("/Requesters").then(res => {
				this.setState({ loadRequester: res.data.data })
			})
		}
	}
	render() {
		let propsTitle = !this.state.isUpdate ? "Add" : "Update";
		let lstRequester = []
		const tempdata = this.state.loadRequester
		for (var index in tempdata) {
			lstRequester.push(<option key={index} value={tempdata[index].ID}>{tempdata[index].RequesterName}</option>)
		}
		let view = (
			<>
				<div className="menubar">

					<div className="row text-left">
						<div className="col-2"></div>
						<div className="col-8">
							<Card>
								<CardHeader tag="h4">{propsTitle} Work</CardHeader>
								<CardBody className="padding-20 font-weight-bold">
									{this.state.id!=='1'?<div className="form-group">
										<label>Work ID</label>
										<input name="id" type="text" className="form-control" disabled value={this.state.id} />
									</div>:''}
									
									<div className="form-group">
										<label>Requester</label>
										<select name="requesterid" type="text" className="form-control" required onChange={this.onChange} value={this.state.requesterid}>
											<option key="select">Select</option>
											{lstRequester}
										</select>
										{this.state.error.requesterid}
									</div>
									<div className="form-group">
										<label>Entry Date</label>
										<div>
											<DatePicker
												dateFormat="dd-MM-yyyy HH:mm:ss"
												showTimeSelect
												timeIntervals={1}
												className="form-control"
												selected={this.state.startdate}
												onChange={(date) => this.setState({ startdate: date })}
											/>
										</div>
										{this.state.error.startdate}
									</div>
									<div className="form-group">
										<label>Expiry Date</label>
										<div>
											<DatePicker
												className="form-control"
												dateFormat="dd-MM-yyyy HH:mm:ss"
												showTimeSelect
												timeIntervals={1}
												selected={this.state.expiredate}
												onChange={(date) => this.setState({ expiredate: date })}
											/>
										</div>
										{this.state.error.expiredate}
									</div>
									<div className="form-group">
										<label>Image File Name</label>
										<div className="row">
											<div className="col-10">
												<input name="filename" type="text" className="form-control" value={this.state.filename} onChange={this.onChange}  />	</div>
											<div className="col-2">
												<Button color="info" onClick={this.onPreview}>Preview</Button>
											</div>
										</div>
										<img src={this.state.filepath} />
										{this.state.error.filename}
									</div>

									<div className="text-right">
										<Button color="primary" onClick={this.onSubmit}>Submit</Button>{'  '}
										<Button color="secondary" onClick={this.clear}>Clear</Button>
									</div>
								</CardBody>
							</Card>
						</div>
						<div className="col-2"></div>
					</div>
				</div>
			</>
		)
		return view;
	}
}
export default Work;